@import '../../global';

.menu {
  width: 0px;
  height: calc(100vh - 70px);
  background-color: $mainColor;
  position: absolute;
  top: 70px;
  right: -300px;
  z-index: 2;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  transition: all .7s ease-in-out;

  &.active {
    right: 0;
width: 300px;
  }

  ul {
    margin: 0;
    padding: 0;
    list-style: none;
    font-size: 40px;
    color: #fff;
    font-weight: 300;
    width: 75%;


    li {
      margin-bottom: 27px;
      transition: all .3s ease;

      a {
        text-decoration: none;
        font-size: inherit;
        color: inherit;
      }

      &:hover {
        font-weight: 500;
        color: #9c9aa0;

      }
    }
  }
}