@import '../../../global';

.aboutWork {
  background-color: #fff;
  height: calc(100vh - 70px);
  margin-top: 70px;
  width: 100vw;
  position: relative;

  .arrow {
    font-size: 90px;
    font-weight: 900;
    position: absolute;
    animation: blink 1s infinite;
    transition: all .4s ease-out;
    left: 10px;
    color: #15023a;

    top: 40px;
    @include mobile {
      display: none;
    }
    @keyframes blink {
      50% {
        left: 50px
      }
    }
  }

  .wrapper {
    //border: 1px solid;
    max-width: 1400px;
    margin: 0 auto;
    height: 100%;
    display: flex;
    align-items: center;
    @include mobile {
      flex-direction: column;

    }

    .left {
      height: 100%;
      //background-color: whitesmoke;
      flex: 1;
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      overflow: hidden;
      @include mobile {
        justify-content: none;
      }

      .back {
        font-size: 30px;
        color: $mainColor;
        font-weight: 600;
        display: none;
        @include mobile {
          display: block;
        }
      }

      @include mobile {

        justify-content: flex-end;

      }

      .imgBox {
        width: 90%;
        height: auto;
        display: flex;
        transform: rotate(10deg);
        transition: all .6s ease-in;
        border: 1px solid;
        @include mobile {
          height: 85%;
          width: 100%;
          transform: rotate(0deg);

        }

        img {
          @include mobile {

            width: 100vw;
            transform: rotate(0deg);
            object-position: center;
          }
        }

        &:hover {
          transform: scale(.8);
          position: absolute;
          z-index: 10;
          left: 0;
          transition: all .6s ease-in;
          @include mobile {
            transform: scale(1);
            position: relative;
            z-index: 10;
            left: 0;
            transition: all .6s ease-in;

          }
        }
      }
    }

    .right {
      flex: 1;
      height: 60%;
      //border: 1px solid;
      display: flex;
      justify-content: space-around;
      flex-direction: column;
      border-left: 4px solid $mainColor;
      @include mobile {
        border:none
      }
      .text {

        display: flex;
        flex-direction: column;
        align-items: center;
        //border: 1px solid;
        height: 50%;
        justify-content: space-between;

        h1 {
          font-size: 70px;
          color: $mainColor;
          font-weight: 700;
          letter-spacing: .03em;
          @include mobile {
            font-size: 40px;
          }
        }

        p {
          max-width: 600px;
          display: flex;
          align-items: center;
          text-align: center;
          color: $mainColor;
          font-size: 27px;
          font-weight: 300;
          @include mobile {
            font-size: 23px
          }
        }

        span {
          font-size: 30px;
          font-weight: 700;
          color: crimson;
          @include mobile {
            font-size: 24px;
          }
        }

      }

      .btn {

        display: flex;
        align-items: center;
        justify-content: space-between;

        height: 20%;
        width: 60%;
        margin: 0 auto;
        @include mobile {
          width: 100%;
        }

        a {
          font-size: 24px;
          border: 2.5px solid $mainColor;
          color: $mainColor;
          transition: all .3s ease-out;
          text-decoration: none;
          cursor: pointer;
          border-radius: 10px;
          padding: 10px 20px;
          @include mobile {
            font-size: 18px;

          }

          &:hover {
            border-color: crimson;
            color: crimson;

          }
        }


      }

    }


  }
}

.wait {
  background-color: crimson;
  height: calc(100vh - 70px);
  margin-top: 70px;
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;

  p {
    color: #15023a;
    font-size: 50px;
    font-weight: 600;
    margin-top: 50px;
  }

  h1 {
    font-size: 100px;
  }
}