@import '../../global';

.portfolio {
  background-color: crimson;
  display: flex;
  flex-direction: column;
  align-items: center;

  h1 {
    font-size: 50px;
    @include mobile {
      font-size: 40px;
    }
  }

  ul {
    margin: 10px;
    list-style: none;
    display: flex;
    @include mobile {
      flex-wrap: wrap;
      justify-content: center;
      margin: 0;
    }
    @include decs {
      flex-wrap: wrap;
      justify-content: center;
      margin: 0;
    }

  }

  .container {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;

    flex-wrap: wrap;
    @include mobile {
      width: 100%;
    }
    @include decs {
      width: 80%;
    }
    .item {
      width: 400px;
      height: 330px;
      //border-right: 20px;
      //border: 1px solid lightgrey;
      margin: 10px 20px;
      display: flex;
      align-items: center;
      justify-content: center;
      color: #fff;
      position: relative;
      transition: all .5s ease-out;
      border-radius: 15px;
      cursor: pointer;
      @include decs {
        margin-top: 50px;
        width: 300px;
        height: 250px;
      }
      @include mobile {
        width: 130px;
        height: 100px;
      }

      div {
        position: absolute;
        margin-top: -600px;
        transition: all 1s ease;
        display: flex;

        //border: 2px solid red;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        width: 100%;

        h3 {
          z-index: 2;
          font-size: 40px;
          margin-bottom: 20px;
          transition: all 1s ease;
          @include mobile {
            font-size: 15px;
          }

        }

        div {
          position: relative;
          display: flex;
          justify-content: center;
          align-items: center;
          margin: 0 auto;

          .btn {

            //position: relative;
            z-index: 2;
            text-decoration: none;
            width: 50%;

            //font-size: 18px;
            //color: #fff;

            button {
              width: 100%;
              border: 2px solid #fff;
              font-size: 18px;
              color: #fff;
              transition: all .4s ease;
              @include mobile {
                font-size: 10px;
                width: 40%;
              }
            }

          }

          &:hover {

            .btn {

              button {
                border: 2px solid crimson;
                color: crimson;
              }
            }


          }


        }
      }


      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        z-index: 1;
      }

      &:hover {
        background-color: $mainColor;

        div {
          margin-top: 0;

          div {


          }
        }


        img {
          opacity: .2;
          z-index: 1;
        }
      }
    }
  }
}