.app {
  height: 100vh;

  overflow: hidden;

  .section::-webkit-scrollbar {
    display: none;
  }

  .section {
    width: 100%;
    height: calc(100vh - 70px);
    background-color: aqua;
    position: relative;
    top: 70px;
    scroll-behavior: smooth;
    scroll-snap-type: y mandatory;
    scrollbar-width: none;


    > * {
      width: 100vw;
      height: calc(100vh - 70px);
      scroll-snap-align: start;
    }
  }
}