@import '../../global';

.contact {
  background-color: #fff;
  display: flex;
  @include mobile {
    flex-direction: column;
  }

  .left {
    flex: 1;
    overflow: hidden;

    img {
      height: 90%;

    }
  }

  .right {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;


    h2 {
      font-size: 40px;
      @include mobile {
        font-size: 30px;
      }
    }


    form {
      width: 80%;
      height: 60%;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-around;
      overflow: hidden;

      position: relative;
      @include mobile {
        width: 100%;
        height: 100%;

      }

      .input {

        overflow: hidden;
        padding: 20px;
        font-size: 15px;
        width: 70%;
        height: 20px;
        outline: none;

        &:focus {
          border-color: crimson;
        }

        @include mobile {
          height: 0px;
        }
      }

      .textarea {
        height: 230px;
        padding: 20px;
        width: 70%;
        overflow: hidden;
        font-size: 15px;

        &:focus {
          border-color: crimson;

        }

        @include mobile {
          height: 70px;
        }
      }

      button {
        //width: 150px;
        //height: 30px;
        //color: whitesmoke;
        //background-color: crimson;
        //font-size: 20px;
        //border: none;
        //border-radius: 10px;
        //font-weight: 500;
        //cursor: pointer;
        color: crimson;
        border: 2px solid crimson;
        font-size: 20px;
        padding: 7px 35px;
      }

      span {
        color: crimson;

      }
    }
  }
}